<template>
  <div class="main">
    <div class="main-top">
      <div class="background"></div>
      <div class="panel-user-info">
        <div class="logo">
          <img v-if="logo" :src="logo" />
        </div>

        <div class="variation-count">
          <div class="variation-count__wrapper">
            <div class="variation-count__icon">
              <i class="fa fa-archive" aria-hidden="true"></i>
            </div>
            <div class="variation-count__value">
              <div class="variation-count__value__denominator">/{{variationCount.all}}</div>
              <div class="variation-count__value__numerator">{{variationCount.owned}}</div>
            </div>
          </div>
        </div>
      </div>

      <div id="toolNav">
        <div class="panel-search">
          <div class="input-wrapper">
            <el-input v-model="term" @input="debounceInput"></el-input>
            <div class="input-icon">
              <i class="fa fa-search" aria-hidden="true"></i>
            </div>
          </div>
        </div>

        <div class="panel-nav">
          <ul>
            <li @click="save()">
              <div class="panel-nav__wrapper">
                <div class="panel-nav__wrapper__icon">
                  <i class="fa fa-save" aria-hidden="true"></i>
                </div>
                <div class="panel-nav__wrapper__title">儲存蒐藏</div>
              </div>
            </li>

            <li @click="onShareClick()">
              <div class="panel-nav__wrapper">
                <div class="panel-nav__wrapper__icon">
                  <i class="fa fa-paper-plane-o" aria-hidden="true"></i>
                </div>
                <div class="panel-nav__wrapper__title">分享蒐藏</div>
              </div>
            </li>

            <a href="https://forms.gle/t5AoWM6zzZYYzzeF6" target="_blank">
              <li>
                <div class="panel-nav__wrapper">
                  <div class="panel-nav__wrapper__icon">
                    <i class="fa fa-question-circle-o" aria-hidden="true"></i>
                  </div>
                  <div class="panel-nav__wrapper__title">問題回報</div>
                </div>
              </li>
            </a>
          </ul>
        </div>
      </div>
    </div>

    <div class="content">
      <el-radio-group class="view-mode-switcher" v-model="viewMode" @change="onViewModeChange">
        <el-radio-button label="list">
          <i class="fa fa-list-ul" aria-hidden="true"></i>
        </el-radio-button>
        <el-radio-button label="grid">
          <i class="fa fa-th-large" aria-hidden="true"></i>
        </el-radio-button>
      </el-radio-group>

      <book :viewMode="viewMode" />
    </div>

    <el-dialog
      title="分享碼連結"
      :visible.sync="isShareDialogVisible"
      width="30%"
      style="min-width: 400px;"
    >
      <span>請複製連結進行分享</span>
      <div @click="handleClipboard(shareUrl, $event)" class="share-box">{{ shareUrl }}</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isShareDialogVisible = false">關閉</el-button>
        <el-button type="primary" @click="handleClipboard(shareUrl, $event)">複製</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import book from "./book.vue";
import Codes from "@/utils/codes";
import clipboard from "@/utils/clipboard";
import logo from "@/assets/logo.png";
import { debounce } from "debounce";
import { mapGetters } from "vuex";

export default {
  name: "Main",
  components: {
    book
  },
  data() {
    return {
      isShareDialogVisible: false,
      shareUrl: "",
      logo,
      term: "",
      viewMode: "list",
      scrollFixHeight: 400
    };
  },
  computed: {
    ...mapGetters(["variationCount"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFix);

    const hash = this.$route.params.hash;
    if (hash) {
      this.$store.commit("READ_STATUS", hash);
    } else {
      this.$store.commit("LOAD_STATUS");
    }
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFix);
  },
  methods: {
    scrollFix(event) {
      // fix toolNav
      var top = window.scrollY,
        left = window.scrollX;
      var toolNav = document.getElementById("toolNav");
      if (top > this.scrollFixHeight) {
        toolNav.classList.add("fix-top");
      } else {
        toolNav.classList.remove("fix-top");
      }
    },
    handleClipboard(text, event) {
      clipboard(text, event);
    },
    save() {
      this.$store.commit(
        "SAVE_STATUS",
        Codes.generateHash(this.status.join(""))
      );

      this.$message({
        showClose: true,
        message: "儲存成功，僅於此瀏覽器能夠讀取",
        type: "success"
      });
    },
    onShareClick() {
      Codes.generateURL(this.status.join(""), url => {
        this.isShareDialogVisible = true;
        this.shareUrl = url;
      });
    },
    onViewModeChange(data) {
      this.viewMode = data;
    },

    debounceInput: debounce(function(e) {
      console.log(this.term);
      this.$store.commit("UPDATE_TERM", this.term);
    }, 500)
  }
};
</script>

