<template>
  <div class="wrapper">
    <transition name="fade" mode="out-in">
      <div v-if="viewMode === 'list'" class="list-container" key="list">
        <div>
          <div style="margin-bottom:10px;">
            <div style="display: inline-block;width: 105px;color: #325026;">名稱</div>
            <div style="display: inline-block;width:calc(100% - 105px)">種類</div>
          </div>

          <DynamicScroller
            :items="filterListDatas"
            :min-item-size="64"
            class="scroller"
            :pageMode="true"
          >
            <template v-slot="{ item, index, active }">
              <DynamicScrollerItem
                :item="item"
                :active="active"
                :size-dependencies="[
            item.content
          ]"
                :data-index="index"
                :data-active="active"
                :watchData="false"
                class="collection"
              >
                <div :key="index">
                  <div class="name">
                    <div>{{ item["zhName"] }}</div>
                    <div class="sub-name">{{ item.name }}</div>
                  </div>
                  <div class="variations">
                    <div
                      class="variation"
                      :class="{ 'variation--isOwnd': status[obj.idx] }"
                      v-for="(obj, i) in item.variations"
                      :key="i"
                      @click="toggleVariation(obj.idx)"
                    >
                      <img v-lazy="obj.content.image" />
                    </div>
                  </div>
                </div>
              </DynamicScrollerItem>
            </template>
          </DynamicScroller>
        </div>
      </div>

      <div v-if="viewMode === 'grid'" class="grid-container" key="grid">
        <card
          v-for="(item, index) in filterListDatas"
          :key="index"
          :item="item"
          :index="index"
          :onVariationClick="toggleVariation"
          :onVariationHover="modifyMainImage"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import card from "./card.vue";
import { mapGetters } from "vuex";

export default {
  name: "Book",
  props: {
    viewMode: {
      type: String,
      default: "list"
    }
  },
  components: {
    card
  },
  data() {
    return {
      isShareDialogVisible: false,
      shareUrl: ""
    };
  },
  computed: {
    ...mapGetters(["filterListDatas"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    },
    listDatas() {
      return this.$store.state.listDatas;
    }
  },
  mounted() {},
  methods: {
    toggleVariation(order) {
      const currentStatus = this.status[order];
      this.status[order] = currentStatus ? 0 : 1;
      this.$store.commit("UPDATE_STATUS", this.status);
    },
    modifyMainImage(imgSrc) {
      this.mainImage = imgSrc;
    }
  }
};
</script>

