<template>
  <div class="card">
    <div class="name">{{ item["zhName"] }}</div>
    <div class="sub-name">{{ item.name }}</div>
    <img
      class="main-variation"
      v-lazy="mainImage ? mainImage : item.content.image"
      v-if="false"
    />
    <div class="variations" v-if="item.variations.length > 0">
      <div
        class="variation-container"
        v-for="(obj, i) in item.variations"
        :key="i"
      >
        <img
          class="variation"
          :class="{ 'variation--isOwnd': status[obj.idx] }"
          v-lazy="obj.content.image"
          @click="onVariationClick(obj.idx)"
          @mouseover="onVariationHover(obj.content.image)"
        />
        <span v-if="item.variations.length > 1" class="tooltip">{{
          obj.content.bodyColor_zh
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "card",
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
    index: {
      type: Number,
      default: 0,
    },
    onVariationClick: {
      type: Function,
      required: true,
    },
    onVariationHover: {
      type: Function,
      required: true,
    },
  },
  computed: {
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      },
    },
  },
  data() {
    return {
      mainImage: "",
    };
  },

  mounted() {},
  methods: {},
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.card:first-child {
  margin-top: 12px;
}

.card + .card {
  margin-top: 12px;
}

.card {
  width: 296px;
  background: #ecedef;
  padding: 20px 40px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 10px;

  .name {
    font-size: 22px;
    font-weight: bolder;
  }

  .sub-name {
    font-size: 13px;
    color: #bbb;
  }

  .main-variation {
    width: 128px;
    height: 128px;
    transition: 0.6s linear;
    align-self: center;
  }

  .variations {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;

    .variation-container {
      position: relative;
    }

    .variation {
      display: inline-block;
      cursor: pointer;
      width: 43.2px;
      height: 43.2px;
      opacity: 0.6;
      margin-right: 2px;

      &.variation--isOwnd {
        background-color: #40474e;
        color: #fff;
        opacity: 1;
        border-radius: 3px;
      }
    }

    .variation + .tooltip {
      visibility: hidden;
      width: 120px;
      background-color: #555;
      color: #fff;
      text-align: center;
      border-radius: 6px;
      padding: 5px 0;
      position: absolute;
      z-index: 1;
      bottom: 125%;
      left: 50%;
      margin-left: -60px;
      opacity: 0;
      transition: opacity 0.2s;
      font-size: 12px;

      &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%;
        margin-left: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: #555 transparent transparent transparent;
      }
    }

    .variation:hover + .tooltip {
      visibility: visible;
      opacity: 1;
    }
  }
}
</style>
